:root{background-color:#3d3d3d;font-size:calc(.9em + 1vw);box-sizing:border-box;font-family:'Cairo', sans-serif}.panel{position:absolute;top:50%;left:50%;transform:translateY(-50%) translateX(-50%);display:flex}.calculator{border:2px solid white;margin:auto;height:auto;width:max-content;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(2, auto)}.keyPad{display:grid;margin:0 0 -2px 0;grid-template-columns:repeat(4, auto);grid-template-rows:repeat(6, auto);gap:.1em}.pad{font-size:1.1rem;cursor:pointer}#zero,#clear{grid-column:1 / 3}#equals{grid-column:4 / 5;grid-row:4 / 6}.display{display:flex;width:100%;height:1.4rem;padding:.2em;margin:0;color:white;font-size:1.4rem}
